import React, { useState } from "react";
import {
  CRow,
  CCol,
  CCard,
  CCardBody,
  CCardHeader,
  CFormGroup,
  CLabel,
  CInput,
  CSpinner,
  CButton,
} from "@coreui/react";
import { useSelector, useDispatch } from "react-redux";
import { useHistory, Redirect } from "react-router-dom";
import {
  SET_DATA_SOAL,
  SET_CURRENT_SOAL,
  // ADD_CURRENT_ID_SOAL,
  SET_ID_USER_SOAL,
} from "src/redux/actions";
import endpoints from "src/utils/endpoints";
import request from "src/utils/request";
import Header from "../Header";
import { exams } from "../../../data/config";
import moment from "moment";

function ExamStart() {
  const dispatch = useDispatch();
  const history = useHistory();
  const { current: currentExam, currentUser } = useSelector(
    (state) => state.dataExam
  );
  const { profile } = useSelector((state) => state.user);
  const [isLoading, setIsLoading] = useState(false);
  // ----------------------------------------------------- Methods
  const startExam = async () => {
    setIsLoading(true);
    try {
      const start = await request(null, {
        ...endpoints.startExamUser,
        data: {
          ...endpoints.startExamUser.data,
          id: currentUser.id,
          waktu_mulai: moment().format("YYYY-MM-DD HH:mm"),
        },
      });
      try {
        const first = await request(null, {
          ...endpoints.generateFirsSoal,
          data: {
            ...endpoints.generateFirsSoal.data,
            exam_user_id: currentUser.id,
          },
        });

        if (
          first.data.exam_question_answer.tipe_jawaban == "pilihan_ganda" ||
          first.data.exam_question_answer.tipe_jawaban ==
            "pilihan_ganda_komplex" ||
          first.data.exam_question_answer.tipe_jawaban == "benar_salah" ||
          first.data.exam_question_answer.tipe_jawaban == "survey_lingkungan"
        ) {
          const firstData = {
            ...first.data,
            exam_question_answer: {
              ...first.data.exam_question_answer,
              jawaban: first.data.exam_question_answer.jawaban.map((item) => {
                return {
                  ...item,
                  status_jawaban: "0",
                };
              }),
            },
          };

          dispatch({
            type: SET_DATA_SOAL,
            payload: firstData,
          });

          dispatch({
            type: SET_ID_USER_SOAL,
            payload: first.exam_user_question_id,
          });

          dispatch({
            type: SET_CURRENT_SOAL,
            payload: { index: 0 },
          });
          setIsLoading(false);
        } else if (
          first.data.exam_question_answer.tipe_jawaban == "isian_singkat"
        ) {
          const nextSoal = {
            ...first.data,
            exam_question_answer: {
              ...first.data.exam_question_answer,
              jawaban: first.data.exam_question_answer.jawaban.map((item) => {
                return {
                  ...item,
                  text_jawaban: "",
                };
              }),
            },
          };
          dispatch({
            type: SET_DATA_SOAL,
            payload: nextSoal,
          });
          dispatch({
            type: SET_ID_USER_SOAL,
            payload: first.exam_user_question_id,
          });
          dispatch({
            type: SET_CURRENT_SOAL,
            payload: { index: 0 },
          });
        } else {
          dispatch({ type: SET_DATA_SOAL, payload: first.data });
          dispatch({
            type: SET_ID_USER_SOAL,
            payload: first.exam_user_question_id,
          });
          dispatch({
            type: SET_CURRENT_SOAL,
            payload: { index: 0 },
          });
          setIsLoading(false);
        }
        setIsLoading(false);
        history.push("/exam");
      } catch (error) {
        setIsLoading(false);
        alert(error.error.response.data.messages);
      }
    } catch (error) {
      setIsLoading(false);
      alert(error.error.response.data.messages);
    }
  };

  const isLoggedIn = () => (profile.nama ? true : false);

  if (!isLoggedIn()) {
    return <Redirect to="/login" />;
  }

  return (
    <>
      <Header>
        <CRow className="d-flex justify-content-center">
          <CCol md={6}>
            <CCard className="card-rounded shadow border-white d-flex flex-wrap mt-2">
              <CCardHeader className="d-flex direction-row justify-content-between align-items-center card-rounded border-0">
                <h3>Konfirmasi Tes</h3>
              </CCardHeader>
              <CCardBody className="pt-0">
                <CFormGroup>
                  <CLabel htmlFor="name">Nama Tes</CLabel>
                  <CInput
                    readOnly
                    placeholder="Input Nama Tes"
                    onChange={(e) => {}}
                    value={exams[currentExam.exam_id]}
                    required
                  />
                </CFormGroup>
                <CFormGroup>
                  <CLabel htmlFor="name">Status Tes</CLabel>
                  <CInput
                    readOnly
                    placeholder="Input Status Tes"
                    onChange={(e) => {}}
                    value={"Baru"}
                    required
                  />
                </CFormGroup>
                <CFormGroup>
                  <CLabel htmlFor="name">Waktu Tes</CLabel>
                  <CInput
                    readOnly
                    placeholder="Input Waktu Tes"
                    onChange={(e) => {}}
                    value={moment(currentExam.waktu_mulai).format(
                      "DD/MM/YYYY HH:mm"
                    )}
                    required
                  />
                </CFormGroup>
                <CFormGroup>
                  <CLabel htmlFor="name">Alokasi Waktu Tes</CLabel>
                  <CInput
                    readOnly
                    placeholder="Input Alokasi Waktu"
                    onChange={(e) => {}}
                    value={`${currentExam.lama_pengerjaan_soal} Menit`}
                    required
                  />
                </CFormGroup>
                <CRow>
                  <CCol xs={12}>
                    <CButton
                      disabled={isLoading}
                      block
                      onClick={() => startExam()}
                      color="primary"
                    >
                      {isLoading && <CSpinner grow size="sm" />}Mulai
                    </CButton>
                  </CCol>
                </CRow>
              </CCardBody>
            </CCard>
          </CCol>
        </CRow>
      </Header>
    </>
  );
}

export default ExamStart;
